import { _decorator, Component, Node, Prefab, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Energy')
export class Energy extends Component {
    @property({ type: Prefab })
    energyrPre: Prefab | null = null;

    start() {
        this.energy();
    }

    update(deltaTime: number) {

    }

    energy() {
        this.schedule(() => {
            let energy = instantiate(this.energyrPre);
            energy.setParent(this.node.parent);
            let node = this.node.position;
            let energyX = node.x - 125;
            energy.setPosition(energyX + 25, node.y, 0);
        }, 0.5)
    }
}

